:root {
  --main-color: #4d5ae5;
  --main-text-color: #2e2f42;
  --p-color: #434455;
  --card-buttons-color: #f4f4fd;
  --acent-color: #404bbf;
  --main-font: "Roboto", sans-serif;
  --main-white-color: #fff;
  --main-border-color: #e7e9fc;
  --footer-socials-bcg: rgba(255, 255, 255, .1);
  --footer-socials-bcg-hover: #31d0aa;
  --customers-color: #afb1b8;
  --form-label-color: #8e8f99;
  --form-input-border-color: rgba(33, 33, 33, .2);
}

body {
  font-family: var(--main-font);
  color: var(--p-color);
  background-color: var(--main-white-color);
  margin: 0;
  font-size: 16px;
}

.blocked {
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p, li {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
  display: block;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.link {
  text-decoration: none;
}

@media (min-width: 768px) {
  .current:after {
    content: "";
    height: 4px;
    width: 100%;
    background-color: var(--acent-color);
    border-radius: 2px;
    position: absolute;
    bottom: -1px;
    left: 0;
  }
}

.container {
  max-width: 428px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1158px;
  }
}

.header {
  border-bottom: 1px solid var(--main-border-color);
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.logo {
  color: var(--main-text-color);
  letter-spacing: .03em;
  font-family: Raleway;
  font-size: 18px;
  font-style: normal;
  font-weight: 800;
  line-height: 1.3;
}

.logo-title {
  color: var(--main-color);
}

.main-nav {
  justify-content: space-between;
